import { createRow } from '@/components/row';
import { createPart } from '@/components/part';

import big from '@/svg/center-row/big.svg';
import bigWithRecess from '@/svg/center-row/big-recess.svg';
import small from '@/svg/center-row/small.svg';
import smallLeftTongue from '@/svg/center-row/small-left-tongue.svg';

const rowCenter = createRow();

const left = createPart(smallLeftTongue, [62, 256], 'Партизан', true);
const bigLeft = createPart(big, [240, 256], 'Партизан');
const smallLeft = createPart(small, [126, 256], 'Партизан', true);
const centerWithRecess = createPart(bigWithRecess, [240, 256], 'Партизан');
const smallRight = createPart(small, [126, 256], 'Партизан', true);

for (const elem of [left, bigLeft, smallLeft, centerWithRecess, smallRight]) {
  rowCenter.appendChild(elem);
}

export default rowCenter;
